// option
export default {
  name: 'OptionSlot',
  props: {
    // 
    optionType: {
      type: String,
      required: true
    },
    // options  text
    //  element-ui  checkbox  radio ,text  content 
    //  <el-checkbox label="value">text</el-checkbox>
    //  optionText , content,  attrs 
    optionText: {
      type: String,
      required: true
    },
    // options  value
    optionValue: {
      type: String,
      required: true
    },
    // option 
    optionAttrs: Object,
    // 
    data: {
      type: [Object, String],
      required: true
    },
    //  text  value
    prop: {
      type: Object,
      default: () => ({
        text: 'text',
        value: 'value'
      })
    }
  },
  render (h) {
    let data = this.changeToObject(this.data)
    data = this.changeProp(data, this.prop)
    const config = this.getOptionConfig(this.optionAttrs, this.optionText, this.optionValue, data)
    return h(this.optionType, config.attrs, config.children)
  },
  methods: {
    // 
    getOptionConfig (customAttrs, textKey, valueKey, data) {
      const attrs = Object.assign({}, customAttrs, { [valueKey]: data['value'] })
      let children = null
      if (textKey === 'content') {
        children = data['text']
      } else {
        attrs[textKey] = data['text']
      }

      return { attrs: { attrs }, children: children }
    },
    // 
    // '' => { 'text': '', 'value': '' }
    changeToObject (str) {
      if (typeof str === 'string') {
        return { text: str, value: str }
      } else {
        return str
      }
    },
    // key
    //  data: { label: '', val: 1 }, prop: { text: 'label', value: 'val' }
    // : data: { text: '', value: 1 }
    changeProp (data, prop) {
      if (prop) {
        return { 'text': data[prop.text], 'value': data[prop.value] }
      } else {
        return data
      }
    }
  }
}
